import React from 'react';

// import './AppHeaderNavLink.css';

// define a Navigation Link type for our links
export type NavLink = {
  label: string;
  route: string;
};

const navLinkItems: NavLink[] = [
    {
      label: '은송❤️잔',
      route: '/',
    },
    {
      label: '오시는길',
      route: '/',
    }
  ]

function AppHeaderNavLink() {
  const navItemsInList = navLinkItems.map((item)=>
    <li>{item.label}</li>
  );
  return (
    <ul>
      {navItemsInList}
    </ul>
  );

}
// const AppHeaderNavLink: React.FC<NavLink> = React.memo(({ label }) => (
//   <span className="nav-link">{label}</span>
// ));

export default AppHeaderNavLink;
